<template>
  <div class="page-hero" v-if="model">
    <div class="topbar hero_top py-2 px-4 d-flex ai-center text-white">
      <img src="../assets/logo (1).png" height="30">
      <div class="px-2 flex-1">
        <span class="text-white">王者荣耀</span>
        <span class="ml-3">攻略站</span>
      </div>
      <router-link to="/" tag="div">更多英雄 &gt;</router-link>
    </div>
    <!-- body -->
    <div class="top" :style="{'background-image':`url(${model.banner})`}">
      <div class="info text-white p-3 h-100 d-flex flex-column jc-end">
        <div class="fs-sm">{{model.title}}</div>
        <h2 class="my-2">{{model.name}}</h2>
        <div class="fs-sm">{{model.categories.map(v => v.name).join('/')}}</div>
        <div class="d-flex jc-between pt-2">
          <div class="scores d-flex ai-center" v-if="model.scores">
            <span>难度</span>
            <!-- model.scores.difficult 在取这种值的时候 要确保前面是有值的  要不然会报错 -->
            <span class="badge bg-orange">{{model.scores.difficult}}</span>
            <span>技能</span>
            <span class="badge bg-blue-1">{{model.scores.skills}}</span>
            <span>攻击</span>
            <span class="badge bg-red-1">{{model.scores.attack}}</span>
            <span>生存</span>
            <span class="badge bg-yellow">{{model.scores.survive}}</span>
          </div>
          <router-link tag="span" class="text-grey fs-sm" to="/">皮肤:2 &gt;</router-link>
        </div>
      </div>
    </div>
    {{model.name}}
  </div>
</template>

<script>
  export default {
    props: {
      id: { requied: true }
    },
    data() {
      return {
        model: null
      }
    },
    methods: {
      async fetch() {
        const res = await this.$http.get(`heroes/${this.id}`)
        this.model = res.data
      }
    },
    created() {
      this.fetch()
    },
  }
</script>

<style lang="scss">
  .page-hero {
    .top {
      height: 50vw;
      background: #fff no-repeat top center;
      background-size: auto 100%;
    }

    .info {
      background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));

      .scores {
        .badge {
          margin: 0 0.25rem;
          display: inline-block;
          /*需要设置宽高*/
          width: 1rem;
          height: 1rem;
          line-height: 0.9rem;
          text-align: center;
          border-radius: 50%;
          font-size: 0.7rem;
          border: 1px solid rgba(255, 255, 255, 0.2);
        }
      }
    }
  }
</style>