<template>
  <h-card :icon="icon" :title="title">
    <div class="nav jc-between">
      <div class="nav-item" :class="{active: active===i}" v-for="(category,i) in categories" :key="i"
        @click="$refs.list.$swiper.slideTo(i)">
        <div class="nav-link">{{category.name}}</div>
      </div>
    </div>
    <div class="pt-2">
      <swiper ref="list" @slide-change="()=>active = $refs.list.$swiper.realIndex" :options="{autoHeight:true}">
        <swiper-slide v-for="(category,i) in categories" :key="i">
          <!-- <div class="py-2" v-for="n in 6" :key="n">
            <span>[新闻]</span>
            <span>|</span>
            <span>5月26日全服不停机更新公告</span>
            <span>06/02</span>
          </div> -->
          <slot name="items" :category="category"></slot>
        </swiper-slide>
      </swiper>
    </div>
  </h-card>
</template>

<script>
  export default {
    props: {
      icon: { type: String, required: true },
      title: { type: String, required: true },
      categories: { type: Array, required: true },
    },
    data() {
      return {
        active: 0
      }
    }
  }
</script>