<template>
  <div class="page-article" v-if="model">
    <div class="d-flex py-3 px-2 border-bottom">
      <i class="iconfont icon-icon-back text-title"></i>
      <strong class="flex-1 text-ellipsis text-title pl-1 pr-1">
        {{model.title}}
      </strong>
      <div class="text-grey fs-sm">
        2020-2-02
      </div>
    </div>
    <div v-html="model.body" class="px-3 fs-lg body"></div>
    <!-- 相关资讯 -->
    <div class="px-3">
      <div class="d-flex ai-center">
        <i class="iconfont icon-cc-menu-circle"></i>
        <strong class="text-title fs-xl ml-1">相关资讯</strong>
      </div>
      <div class="pt-3">
        <router-link class="py-1  d-flex ai-center" :to="`/articles/${item._id}`" tag="div"
          v-for="item in model.related" :key="item._id">
          <div class="flex-1 text-ellipsis fs-lg">{{item.title}}</div>
          <div class="pl-2 text-grey">2020-2-31</div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
  import '../assets/iconfont/iconfont.css'
  export default {
    props: {
      id: { required: true }
    },
    data() {
      return {
        model: null
      }
    },
    watch: {
      // 监听点击时发生的跳转
      // id:fetch(),
      id() {
        this.fetch()
      }
    },
    methods: {
      async fetch() {
        const res = await this.$http.get(`articles/${this.id}`)
        this.model = res.data
      }
    },
    created() {
      this.fetch()
    },
  }
</script>
<style lang="scss">
  .page-article {
    .icon-back {
      font-size: 1.6923rem;
    }

    .body {
      img {
        max-width: 100%;
        height: auto;
      }

      iframe {
        max-width: 100%;
        height: auto;
      }
    }
  }
</style>